<template>
  <div style="margin:35px">

    <!---->
    <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
      <el-form :inline="true" :model="filters" @submit.native.prevent>
        <router-link :to="{ name: ''}" style='text-decoration: none;color: aliceblue;'>
                <el-button round style="margin-left: 35px"><i class="el-icon-d-arrow-left" ></i> </el-button>
        </router-link>
        <div style="float: right; margin-right: 150px">
          <el-form-item>
            <el-input v-model.trim="filters._id" placeholder="" @keyup.enter.native="getTestReports"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" class="el-icon-search" @click="getTestReports"> </el-button>
          </el-form-item>
        </div>
      </el-form>
    </el-col>

    <!---->
    <el-table @sort-change='sortChange' :data="testReports" highlight-current-row v-loading="listLoading" @selection-change="selsChange" style="width: 100%;">
      <el-table-column type="selection" min-width="5%">
      </el-table-column>
      <el-table-column sortable='custom' prop="_id" label="" min-width="17%" show-overflow-tooltip>
      </el-table-column>
      <el-table-column sortable='custom' prop="testCount" label="" min-width="8%" show-overflow-tooltip>
      </el-table-column>
      <el-table-column sortable='custom' prop="passCount" label="" min-width="8%" show-overflow-tooltip>
      </el-table-column>
      <el-table-column sortable='custom' prop="failedCount" label="" min-width="8%" show-overflow-tooltip>
      </el-table-column>
      <el-table-column sortable='custom' prop="passRate" label="" min-width="8%" show-overflow-tooltip>
      </el-table-column>
      <el-table-column sortable='custom' prop="comeFrom" label="" min-width="10%" show-overflow-tooltip>
      </el-table-column>
      <el-table-column sortable='custom' prop="executorNickName" label="" min-width="10%" show-overflow-tooltip>
      </el-table-column>
      <el-table-column sortable='custom' prop="createAt" label="" min-width="15%" show-overflow-tooltip>
      </el-table-column>
      <el-table-column label="" min-width="15%">
        <template slot-scope="scope">
          <el-button size="small" class="el-icon-document" type="primary" @click="showReportDetail(scope.$index, scope.row)"> </el-button>
          <!--<el-button type="danger" size="small" @click="handleDel(scope.$index, scope.row)"></el-button>-->
        </template>
      </el-table-column>
    </el-table>

    <!---->
    <el-col :span="24" class="toolbar">
      <!--<el-button type="danger" @click="batchRemove" :disabled="this.sels.length===0"></el-button>-->
      <el-pagination
        style="float: right"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :page-sizes="[10, 20, 40]"
        :page-size="size"
        layout="total, sizes, prev, pager, next, jumper"
        :total="totalNum">
      </el-pagination>
    </el-col>

    <!---->
    <el-dialog title="" width="97%" v-loading="detailLoading" :visible.sync="isReportDetailShow" :close-on-click-modal="false">
      <div style="height:700px;overflow:auto;overflow-x:hidden;border: 1px solid #e6e6e6">
        <el-table height="700" :data="testReportDetail" :row-class-name="reportsTableRow" :header-cell-style="reportHeaderColor" v-loading="listLoading" style="width: 100%;">
          <el-table-column prop="testBaseInfo.name" label="" min-width="30%" sortable show-overflow-tooltip>
          </el-table-column>
          <el-table-column prop="testBaseInfo.requestMethod" label="" min-width="15%" sortable show-overflow-tooltip>
          </el-table-column>
          <el-table-column prop="testBaseInfo.url" label="" min-width="15%" sortable show-overflow-tooltip>
          </el-table-column>
          <el-table-column prop="testBaseInfo.headers" label="" min-width="15%" sortable show-overflow-tooltip>
          </el-table-column>
          <el-table-column prop="testBaseInfo.cookies" label="Cookie" min-width="18%" sortable show-overflow-tooltip>
          </el-table-column>
          <el-table-column prop="testBaseInfo.presendParams" label="" min-width="15%" sortable show-overflow-tooltip>
          </el-table-column>
          <el-table-column prop="testBaseInfo.checkHttpCode" label="" min-width="18%" sortable show-overflow-tooltip>
          </el-table-column>
          <el-table-column prop="responseHttpStatusCode" label="" min-width="18%" sortable show-overflow-tooltip>
          </el-table-column>
          <el-table-column prop="testBaseInfo.checkResponseData" label="" min-width="15%" sortable show-overflow-tooltip>
          </el-table-column>
          <el-table-column prop="testBaseInfo.checkResponseNumber" label="" min-width="15%" sortable show-overflow-tooltip>
          </el-table-column>
          <el-table-column prop="testBaseInfo.checkResponseSimilarity" label="" min-width="18%" sortable show-overflow-tooltip>
          </el-table-column>
          <el-table-column prop="responseData" label="" min-width="15%" sortable show-overflow-tooltip>
          </el-table-column>
          <el-table-column prop="testConclusion" label="" min-width="15%" sortable show-overflow-tooltip>
          </el-table-column>
          <el-table-column prop="testStartTime" label="" min-width="25%" sortable show-overflow-tooltip>
          </el-table-column>
          <el-table-column prop="spendingTimeInSec" label="/s" min-width="18%" sortable show-overflow-tooltip>
          </el-table-column>
        </el-table>
      </div>
    </el-dialog>

  </div>
</template>
<script>
    import {getReportList, getReportDetail} from "../../api/testReport";
    export default {
        data () {
            return {
                listLoading: false,
                detailLoading: false,
                isReportDetailShow: false,
                testReports: [],
                testReportDetail: [],
                sels: [],
                size: 10,
                skip: 0,
                sortBy: 'createAt',
                order: 'descending',
                pageNum: 1,
                totalNum: 0,
                filters: {
                  _id: ''
                },
            }
        },
        mounted(){
          this.getTestReports()
        },
        methods: {
            selsChange: function (sels) {
              this.sels = sels;
            },
            getTestReports() {
                let self = this;
                self.listLoading = true;
                let params = {size: self.size, skip: self.skip, sortBy: self.sortBy, order: self.order,
                projectId: self.$route.params.project_id};
                if (self.filters._id.trim() !== ''){
                  params['_id'] = self.filters._id.trim()
                };
                let header = {};
                getReportList(self.$route.params.project_id, params, header).then((res) => {
                  self.listLoading = false;
                  let {status, data} = res;
                  if (status === 'ok'){
                    self.testReports = data.rows;
                    self.totalNum = data.totalNum;
                  }else {
                    self.$message.error({
                      message: data,
                      center: true,
                    });
                  }
                }).catch((error) => {
                  console.log(error)
                  self.$message.error({
                      message: '~',
                      center: true,
                  });
                  self.listLoading = false;
                })
            },
            showReportDetail(index, row){
              let self = this;
              self.isReportDetailShow = true;
              self.detailLoading = true;
              let header = {};
              getReportDetail(self.$route.params.project_id, row._id , header).then((res) => {
                self.detailLoading = false;
                let {status, data} = res;
                if (status === 'ok'){
                  let testDetails = data["testDetail"] || [];
                  if (testDetails.length) {
                    testDetails.forEach((item, index, array) => {
                      item["testBaseInfo"]["headers"] = JSON.stringify(item["testBaseInfo"]["headers"]) || '';
                      item["testBaseInfo"]["cookies"] = JSON.stringify(item["testBaseInfo"]["cookies"]) || '';
                      item["testBaseInfo"]["presendParams"] = JSON.stringify(item["testBaseInfo"]["presendParams"]) || '';

                      // TODO 

                      item["responseData"] ?
                       item["responseData"].toString().trim() === '' ?
                        item["responseData"] = '()':
                        item["responseData"] = item["responseData"]:
                       item["responseData"] = '()'


                      item["responseHttpStatusCode"] ?
                       item["responseHttpStatusCode"].toString().trim() === '' ?
                        item["responseHttpStatusCode"] = '()':
                        item["responseHttpStatusCode"] = item["responseHttpStatusCode"]:
                       item["responseHttpStatusCode"] = '()'

                      if (item["testBaseInfo"]["checkResponseData"] === undefined || item["testBaseInfo"]["checkResponseData"] === null || (item["testBaseInfo"]["checkResponseData"].length === 1
                          && item["testBaseInfo"]["checkResponseData"][0]['regex'].trim() === '')){
                        item["testBaseInfo"]["checkResponseData"] = '()'
                      }else{
                        item["testBaseInfo"]["checkResponseData"] = JSON.stringify(item["testBaseInfo"]["checkResponseData"]) || '';
                      }
                      if (item["testBaseInfo"]["checkResponseNumber"] === undefined || item["testBaseInfo"]["checkResponseNumber"] === null || (item["testBaseInfo"]["checkResponseNumber"].length === 1
                          && item["testBaseInfo"]["checkResponseNumber"][0]['expressions']['expectResult'].trim() === '')){
                        item["testBaseInfo"]["checkResponseNumber"] = '()'
                      }else{
                        item["testBaseInfo"]["checkResponseNumber"] = JSON.stringify(item["testBaseInfo"]["checkResponseNumber"]) || '';
                      }
                      if (item["testBaseInfo"]["checkResponseSimilarity"] === undefined || item["testBaseInfo"]["checkResponseSimilarity"] === null || (item["testBaseInfo"]["checkResponseSimilarity"].length === 1
                          && item["testBaseInfo"]["checkResponseSimilarity"][0]['compairedText'].trim() === '')){
                        item["testBaseInfo"]["checkResponseSimilarity"] = '()'
                      }else{
                        item["testBaseInfo"]["checkResponseSimilarity"] = JSON.stringify(item["testBaseInfo"]["checkResponseSimilarity"]) || '';
                      }

                      if (item["testBaseInfo"]["presendParams"] === 'null' || item["testBaseInfo"]["presendParams"] === '{}'){
                        item["testBaseInfo"]["presendParams"] = '()'
                      }
                      if (item["testBaseInfo"]["headers"] === 'null'){
                        item["testBaseInfo"]["headers"] = '()'
                      }
                      if (item["testBaseInfo"]["cookies"] === '[]'){
                        item["testBaseInfo"]["cookies"] = '(Cookie)'
                      }
                      if (item["testBaseInfo"]["checkHttpCode"] === null || item["testBaseInfo"]["checkHttpCode"] === undefined){
                        item["testBaseInfo"]["checkHttpCode"] = '()'
                      }
                    });
                  }
                  self.testReportDetail = testDetails
                }else{
                  self.$message.error({
                    message: data,
                    center: true,
                  });
                }
              }).catch((error) => {
                  console.log(error)
                  self.$message.error({
                      message: '~',
                      center: true,
                  });
                  self.detailLoading = false;
              });
            },
            handleSizeChange(val){
              let self = this;
              self.size = val;
              self.listLoading = true;
              let params = {size: self.size, skip: self.skip, sortBy: self.sortBy, order: self.order,
                projectId: self.$route.params.project_id};
              let header = {};
              getReportList(self.$route.params.project_id, params, header).then((res) => {
                self.listLoading = false;
                let {status, data} = res;
                if (status === 'ok'){
                  self.testReports = data.rows;
                  self.totalNum = data.totalNum;
                }else {
                  self.$message.error({
                    message: data,
                    center: true,
                  });
                }
              }).catch((error) => {
                  self.$message.error({
                      message: '~',
                      center: true,
                  });
                  self.listLoading = false;
                })
            },
            handleCurrentChange(val){
              let self = this;
              self.skip = (val - 1 ) * self.size;
              self.listLoading = true;
              let params = {size: self.size, skip: self.skip, sortBy: self.sortBy, order: self.order,
                            projectId: self.$route.params.project_id};
              let header = {};
              getReportList(self.$route.params.project_id, params, header).then((res) => {
                self.listLoading = false;
                let {status, data} = res;
                if (status === 'ok'){
                  self.testReports = data.rows;
                  self.totalNum = data.totalNum;
                }else {
                  self.$message.error({
                    message: data,
                    center: true,
                  });
                }
              }).catch((error) => {
                  self.$message.error({
                      message: '~',
                      center: true,
                  });
                  self.listLoading = false;
                })
            },
            // table tr
            reportsTableRow({ row, rowIndex }){
              if (row.status.toString() === 'ok')
                return 'bg1-row reportsTableRow'
              else {
                return 'bg2-row reportsTableRow'
              }
            },
            // table header
            reportHeaderColor({ row, column, rowIndex, columnIndex }) {
              if (rowIndex === 0) {
                return 'background-color: #1E90FF;color: #fff;font-weight: 500;'
              }
            },
            //
            sortChange (column){
              let self = this;
              self.listLoading = true;
              self.sortBy = column.prop;
              self.order = column.order;
              let params = {size: self.size, skip: self.skip, sortBy: self.sortBy, order: self.order,
                projectId: self.$route.params.project_id};
              let header = {};
              getReportList(self.$route.params.project_id, params, header).then((res) => {
                self.listLoading = false;
                let {status, data} = res;
                if (status === 'ok'){
                  self.testReports = data.rows;
                  self.totalNum = data.totalNum;
                }else {
                  self.$message.error({
                    message: data,
                    center: true,
                  });
                }
              }).catch((error) => {
                  self.$message.error({
                      message: '~',
                      center: true,
                  });
                  self.listLoading = false;
                })
            }
        },
        watch: {
            time(){

            }
        }
    }
</script>

<style lang="scss">
  .demo-table-expand {
      font-size: 0;
  }
  .el-tooltip__popper{
    max-width:60%
  }
  .el-table .el-table__body .reportsTableRow:hover>td {
    background-color: deepskyblue;
  }
  .el-table .bg1-row{
    background-color: #33CC00;
    color: #fff;
    font-weight: 500;
  }
  .el-table .bg2-row{
    background-color: #FF3333;
    color: #fff;
    font-weight: 500;
  }
</style>



